'use client';

import React, { useState } from 'react';
import { useFormik } from 'formik';
import { emailRegisterSchema, mobileRegisterSchema } from '@/schemas/auth';

import PhoneInput from '../../molecules/LoginInputs/PhoneInput/PhoneInput';
import EmailInput from '../../molecules/LoginInputs/EmailInput/EmailInput';

import styles from './RegisterForm.module.scss';

const RegisterForm = () => {
    const [registerMethod, setRegisterMethod] = useState<'email' | 'mobile'>('email');

    const formik = useFormik({
        initialValues: {
            name: '',
            nickname: '',
            country: '',
            uidCode: '',
            email: '',
            phoneNumber: '',
            countryCode: '+90',
            password: '',
            confirmPassword: '',
        },
        validationSchema:
            registerMethod === 'email' ? emailRegisterSchema : mobileRegisterSchema,
        onSubmit: (values) => {
            console.log('KayÄ±t baÅŸarÄ±yla gÃ¶nderildi!', values);
        },
    });

    const handleAuthenticate = () => {
        if (registerMethod === 'email') {
            console.log('Email ile doÄŸrulama gÃ¶nderildi:', formik.values.email);
        } else {
            const fullPhone = formik.values.countryCode + formik.values.phoneNumber;
            console.log('Telefon ile doÄŸrulama gÃ¶nderildi:', fullPhone);
        }
    };

    return (
        <form onSubmit={formik.handleSubmit} className={styles.registerForm}>
            <h2>Register to Rockie</h2>

            <div className={styles.toggleButtons}>
                <button
                    type="button"
                    className={registerMethod === 'email' ? styles.active : ''}
                    onClick={() => setRegisterMethod('email')}
                >
                    Email
                </button>
                <button
                    type="button"
                    className={registerMethod === 'mobile' ? styles.active : ''}
                    onClick={() => setRegisterMethod('mobile')}
                >
                    Mobile
                </button>
            </div>

            <input
                name="name"
                type="text"
                placeholder="Full Name"
                value={formik.values.name}
                onChange={formik.handleChange}
                className={styles.input}
            />
            {formik.touched.name && formik.errors.name && (
                <div className={styles.error}>{formik.errors.name}</div>
            )}

            <input
                name="nickname"
                type="text"
                placeholder="Nickname"
                value={formik.values.nickname}
                onChange={formik.handleChange}
                className={styles.input}
            />
            {formik.touched.nickname && formik.errors.nickname && (
                <div className={styles.error}>{formik.errors.nickname}</div>
            )}

            <input
                name="country"
                type="text"
                placeholder="Country"
                value={formik.values.country}
                onChange={formik.handleChange}
                className={styles.input}
            />
            {formik.touched.country && formik.errors.country && (
                <div className={styles.error}>{formik.errors.country}</div>
            )}

            <input
                name="uidCode"
                type="text"
                placeholder="UID Code"
                value={formik.values.uidCode}
                onChange={formik.handleChange}
                className={styles.input}
            />
            {formik.touched.uidCode && formik.errors.uidCode && (
                <div className={styles.error}>{formik.errors.uidCode}</div>
            )}

            <div className={styles.inputGroup}>
                {registerMethod === 'email' ? (
                    <EmailInput
                        email={formik.values.email}
                        onEmailChange={(value) => formik.setFieldValue('email', value)}
                        onAuthenticateClick={handleAuthenticate}
                    />
                ) : (
                    <PhoneInput
                        countryCode={formik.values.countryCode}
                        phoneNumber={formik.values.phoneNumber}
                        onCountryCodeChange={(value) =>
                            formik.setFieldValue('countryCode', value)
                        }
                        onPhoneNumberChange={(value) =>
                            formik.setFieldValue('phoneNumber', value)
                        }
                        onAuthenticateClick={handleAuthenticate}
                    />
                )}
                {registerMethod === 'email' &&
                    formik.touched.email &&
                    formik.errors.email && (
                        <div className={styles.error}>{formik.errors.email}</div>
                    )}
                {registerMethod === 'mobile' &&
                    formik.touched.phoneNumber &&
                    formik.errors.phoneNumber && (
                        <div className={styles.error}>{formik.errors.phoneNumber}</div>
                    )}
            </div>

            <input
                name="password"
                type="password"
                placeholder="Password"
                value={formik.values.password}
                onChange={formik.handleChange}
                className={styles.input}
            />
            {formik.touched.password && formik.errors.password && (
                <div className={styles.error}>{formik.errors.password}</div>
            )}

            <input
                name="confirmPassword"
                type="password"
                placeholder="Confirm Password"
                value={formik.values.confirmPassword}
                onChange={formik.handleChange}
                className={styles.input}
            />
            {formik.touched.confirmPassword && formik.errors.confirmPassword && (
                <div className={styles.error}>{formik.errors.confirmPassword}</div>
            )}

            <button type="submit" className={styles.registerButton}>
                Register
            </button>

            <p className={styles.loginPrompt}>
                Already have an account? <a href="/login">Login</a>
            </p>
        </form>
    );
};

export default RegisterForm;
